@import url(https://fonts.googleapis.com/css?family=Hind+Siliguri&display=swap);
body {
  margin: 0;
  font-family: -'Hind Siliguri','Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #148F77;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New'
    monospace;
}
*{font-family:'Hind Siliguri', sans-serif}.container{position:absolute;width:80vw;height:91vh;background-color:#CCD1D1;border:1px solid rgba(0,0,0,0.2);box-shadow:0px 2px 10px rgba(0,0,0,0.1);border-radius:10px;padding:10px;box-sizing:border-box;top:0px;bottom:0px;left:0px;right:0px;margin:auto;overflow-y:auto;overflow:-moz-scrollbars-none;-ms-overflow-style:none;scrollbar-width:none}.container::-webkit-scrollbar{width:0;height:0}.title{position:relative;background-color:#000;color:#fff;padding:20px;font-size:1.8em;font-family:'Hind Siliguri', sans-serif;border-radius:5px;text-align:center}.question{font-size:1.2em;font-weight:bold;margin-bottom:5px;font-family:'Hind Siliguri', sans-serif}.questionBox{position:relative;background-color:#E5E8E8;border:1px solid rgba(0,0,0,0.2);margin-top:5px;padding:10px;box-sizing:border-box}.answerBtnCommon,.answerBtn,.answerBtn_selected{position:relative;display:inline-block;padding:10px;margin:10px;outline:none;border:none;font-size:1em;font-family:'Hind Siliguri', sans-serif;color:#fff;font-weight:400;border-radius:4px;cursor:pointer}.answerBtn{background-color:#138D75}.answerBtn:hover{background-color:#117A65}.answerBtn_selected{background-color:#A6ACAF;transition:1s}.answerBtn_selected:hover{background-color:#BFC9CA}.score-board{position:relative}.score{font-size:2em;padding:5%;text-align:center}.winnerText{color:#148F77}.info{font-size:1em;padding:5%;text-align:center}.playAgain{position:relative;text-align:center}.playBtn{width:150px;padding:10px;top:350px;left:0px;right:0px;font-size:1.4em;color:#F8F9F9;background-color:#196F3D;border:1px solid rgba(0,0,0,0.2);border-radius:30px;box-shadow:0px 2px 10px rgba(0,0,0,0.5);cursor:pointer}.playBtn:hover{background-color:#117A65}.footer{text-align:center}

